
body {
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
      -ms-flex-align: start;
              -ms-grid-row-align: flex-start;
          align-items: flex-start;
}

body, body * {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}

#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: Helvetica, sans-serif, Arial;
  text-align: center;
  color: #fff;
  overflow: hidden;
}

a {
  color: #fff;
  text-decoration: none;
}

.container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.head, .head-bg, .head-title {
  width: 100%;
}

.head {
  position: absolute;
  z-index: 2;
  pointer-events: none;
}

.head-c {
  -webkit-perspective: 1000px;
          perspective: 1000px;
  pointer-events: all;
  height: 58px;
}

.head-c.head-1 .head-left {
  opacity: 1;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.head-c.head-1 .head-right {
  opacity: 0;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.head-c.head-2 .head-left {
  opacity: 0;
}

.head-c.head-2 .head-right {
  opacity: 1;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.head-slide-transition, .head-c-slide-transition {
  -webkit-transition: -webkit-transform .5s ease;
  transition: -webkit-transform .5s ease;
  transition: transform .5s ease;
  transition: transform .5s ease, -webkit-transform .5s ease;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.head-slide-enter, .head-c-slide-enter, .head-slide-leave, .head-c-slide-leave {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.head-bg {
  height: 99px;
}

.head-c .head-bg {
  pointer-events: none;
}

.head-title {
  position: absolute;
  top: 0;
  padding-top: 15px;
  font-size: 20px;
  font-weight: normal;
  color: #ece5f3;
  text-align: center;
  line-height: 30px;
}

.head-title-slide-transition {
  -webkit-transition: opacity 1s ease .5s;
  transition: opacity 1s ease .5s;
  opacity: 1;
}

.head-title-slide-enter, .head-title-slide-leave {
  opacity: 0;
}

.logo {
  width: 100px;
  height: 100px;
  display: block;
  margin: auto;
}



