
.luck-container {
  height: 100%;
}

.match-result.luck {
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.match-result.luck>div {
  min-height: 0;
}

.match-result.luck>div.content-body {
  min-height: 100%;
}

.match-result.luck .copyright {
  bottom: 15px;
}

.cards-stack img {
  width: 81px;
  height: 134px;
  position: absolute;
  left: 50%;
  margin-left: -40px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.cards-stack {
  position: absolute;
  width: 100%;
  height: 200px;
  top: 24%;
  opacity: 0;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}

.cards-stack.expand{
  opacity: 1;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.cards-stack.expand img:nth-of-type(1) {
  -webkit-transform: rotate(-28deg) translate3d(-75px, -25px, 0);
          transform: rotate(-28deg) translate3d(-75px, -25px, 0);
}

.cards-stack.expand img:nth-of-type(2) {
  -webkit-transform: rotate(-21deg) translate3d(-58px, -16px, 0);
          transform: rotate(-21deg) translate3d(-58px, -16px, 0);
}

.cards-stack.expand img:nth-of-type(3) {
  -webkit-transform: rotate(-14deg) translate3d(-40px, -9px, 0);
          transform: rotate(-14deg) translate3d(-40px, -9px, 0);
}

.cards-stack.expand img:nth-of-type(4) {
  -webkit-transform: rotate(-7deg) translate3d(-20px, -4px, 0);
          transform: rotate(-7deg) translate3d(-20px, -4px, 0);
}

.cards-stack.expand img:nth-of-type(5) {
  -webkit-transform: rotate(-1deg) translate3d(0px, -4px, 0);
          transform: rotate(-1deg) translate3d(0px, -4px, 0);
}

.cards-stack.expand img:nth-of-type(9) {
  -webkit-transform: rotate(28deg) translate3d(77px, -21px, 0);
          transform: rotate(28deg) translate3d(77px, -21px, 0);
}

.cards-stack.expand img:nth-of-type(8) {
  -webkit-transform: rotate(21deg) translate3d(59px, -14px, 0);
          transform: rotate(21deg) translate3d(59px, -14px, 0);
}

.cards-stack.expand img:nth-of-type(7) {
  -webkit-transform: rotate(14deg) translate3d(40px, -8px, 0);
          transform: rotate(14deg) translate3d(40px, -8px, 0);
}

.cards-stack.expand img:nth-of-type(6) {
  -webkit-transform: rotate(7deg) translate3d(20px, -4px, 0);
          transform: rotate(7deg) translate3d(20px, -4px, 0);
}

.guide {
  font-size: 14px;
  color: #877d9b;
  position: absolute;
  width: 100%;
  text-align: center;
  top: 24%;
  margin-top: 170px;
  opacity: 0;
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
}

.guide.expand {
  opacity: 1;
}

.card-face {
  position: absolute;
  width: 265px;
  height: 344px;
  top: 50%;
  left: 50%;
  margin-left: -132px;
  margin-top: -177px;
  z-index: 1;
}

.card-face img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.card-face div {
  color: #7d53b5;
  font-size: 15px;
  text-indent: 30px;
  position: absolute;
  padding: 0 30px;
  top: 50%;
  margin: auto;
  width: 100%;
  text-align: justify;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.warning-transition {
  position: absolute;
  width: 100%;
  height: 50%;
  top: 50%;
  padding-top: 170px;
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
  opacity: 1;
}

.warning-transition div {
  position: relative;
  height: 100%;
}

.warning-transition div:after {
  content: '每天只能选择一次哟';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: #877d9b;
  margin: auto;
  -webkit-transform: translateY(-150%);
          transform: translateY(-150%);
}

.warning-enter, .warning-leave {
  opacity: 0;
}



