
.match-content {
  height: 100%;
  width: 100%;
}

.match-wheel-left, .match-wheel-right {
  width: 287px;
  height: 287px;
  border-radius: 100%;
  position: absolute;
  top: 30px;
}

.match-wheel-left {
  left: -140px;
}

.match-wheel-right {
  right: -140px;
}

.match-wheel-left-transition, .match-wheel-right-transition {
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}

.match-wheel-left-enter, .match-wheel-left-leave {
  -webkit-transform: translateX(-147px);
          transform: translateX(-147px);
}

.match-wheel-right-enter, .match-wheel-right-leave {
  -webkit-transform: translateX(147px);
          transform: translateX(147px);
}

.match-wheel-label {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -15px;
  margin-left: 3px;
  margin-right: 3px;
  font-size: 14px;
}

.match-wheel-right .match-wheel-label {
  left: auto;
  right: 50%;
}

.match-wheel-label-transition {
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
  opacity: 1;
}

.match-wheel-label-enter, .match-wheel-label-leave {
  opacity: 0;
}

.match-wheel-label>div {
  font-size: 10px;
  color: #9b94af;
}



