
.wheel {
  box-sizing: content-box;
  width: 402px;
  height: 402px;
  position: absolute;
  left: 50%;
  top: -158px;
  margin-left: -201px;
  overflow: hidden;
}

.wheel-slide-transition {
  -webkit-transition: -webkit-transform 1s ease;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.wheel-slide-enter, .wheel-slide-leave {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.wheel-bg {
  pointer-events: none;
  width: 100%;
  height: 100%;
  margin-top: -100%;
  display: block;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}

.wheel-panel {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.wheel-panel-transition, .wheel-panel-reverse-transition {
  -webkit-transition: -webkit-transform 1s ease;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.wheel-panel-enter {
  -webkit-transform: rotate(-360deg);
          transform: rotate(-360deg);
}

.wheel-panel-reverse-enter {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.horoline {
  width: 215px;
  height: 220px;
  top: 40%;
  left: 50%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  position: absolute;
  overflow: hidden;
}

.horoline-show-transition {
  -webkit-transition: opacity 0.5s linear;
  transition: opacity 0.5s linear;
  opacity: 1;
}

.horoline-show-enter, .horoline-show-leave {
  opacity: 0;
}

.horoline img {
  width: 100%;
  height: 100%;
}

.btn {
  width: 110px;
  height: 110px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  margin-left: -55px;
}

.btn-show-transition {
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
  opacity: 1;
}

.btn-show-enter, .btn-show-leave {
  opacity: 0;
}

.btn .wave {
  display: block;
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  top: 50%;
  left: 50%;
  margin: -50px auto auto -50px;
  opacity: 0;
  background: #6b50a1;
  -webkit-animation: wave 6s linear infinite;
          animation: wave 6s linear infinite;
}

.btn .wave:nth-of-type(1) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.btn .wave:nth-of-type(2) {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.btn .wave:nth-of-type(3) {
  -webkit-animation-delay: 3.5s;
          animation-delay: 3.5s;
}
.btn .wave:nth-of-type(4) {
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
}

@-webkit-keyframes wave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.15;
  }
  100% {
    -webkit-transform: scale(4);
            transform: scale(4);
    opacity: 0;
  }
}

@keyframes wave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.15;
  }
  100% {
    -webkit-transform: scale(4);
            transform: scale(4);
    opacity: 0;
  }
}

.btn a {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}

.btn span {
  z-index: 3;
  line-height: 100%;
  position: absolute;
  width: 100%;
  left: 50%;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  color: #ece5f3;
  font-size: 18px;
  letter-spacing: 1px;
}

.btn img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}



