
.match-result {
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 25px;
  color: #ece5f3;
}

.match-result-transition {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  opacity: 1;
}

.match-result-enter, .match-result-leave {
  opacity: 0;
}

.match-result>div {
  min-height: 100%;
}

.match-result .content-head {
  height: 136px;
}

.match-result .content-head-topline-left,
.match-result .content-head-topline-right,
.match-result .corner-left,
.match-result .corner-right {
  top: 8px;
}

.match-result .content-head-topline-left {
  padding-right: 0;
}

.match-result .content-head-topline-right {
  padding-left: 0;
}

.match-result .content-body {
  padding-top: 136px;
  position: static;
  overflow: hidden;
}

.match-result .content-head-middleline-left, .match-result .content-head-middleline-right {
  top: 24px;
  height: 100px;
}

.match-result .content-body-border-left, .match-result .content-body-border-right {
  padding-top: 148px;
}

.match-icon-left, .match-icon-right {
  position: absolute;
  top: 52px;
  width: 38px;
  height: 38px;
  border-radius: 100%;
  background: rgba(149, 93, 188, 0.5);
}

.match-icon-left {
  left: 30px;
}

.match-icon-right {
  right: 30px;
}

.match-icon-left:before, .match-icon-right:before {
  content: '';
  display: block;
  width: 46px;
  height: 46px;
  margin-left: -5px;
  margin-top: -5px;
  border: 1px solid rgba(149, 93, 188, 0.5);
  border-radius: 100%;
}

.match-icon-left img, .match-icon-right img {
  width: 60%;
  height: 60%;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  opacity: 0.7;
}

.match-icon-left span, .match-icon-right span {
  width: 100px;
  height: 15px;
  line-height: 15px;
  font-size: 12px;
}

.match-heart {
  position: absolute;
  width: 108px;
  height: 90px;
  left: 50%;
  top: 25px;
  margin-left: -54px;
  -webkit-animation: heartbeat 1s ease infinite;
          animation: heartbeat 1s ease infinite;
}

.match-rate {
  position: absolute;
  width: 100px;
  font-size: 20px;
  top: 28px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.match-heart img {
  width: 100%;
  height: 100%;
}

@-webkit-keyframes heartbeat {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.9;
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.9;
  }
}

@keyframes heartbeat {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.9;
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.9;
  }
}

.heart-beat {
  width: 100%;
  height: 62px;
  position: absolute;
  top: 35px;
  padding: 0 73px;
  z-index: -1;
}

.heart-beat:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-image: url(../assets/heart-beat.png);
  background-repeat: repeat-x;
  background-size: contain;
  background-position: 0 0;
}

.match-star {
  width: 75px;
  height: 11px;
  position: absolute;
  top: 135px;
  left: 50%;
  margin-top: 45px;
  background-image: url(../assets/match-star.png);
  background-repeat: repeat-x;
  background-size: contain;
  background-position: 0 0;
}

.match-star:nth-of-type(2n+1) {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  margin-left: -8%;
}

.match-star:nth-of-type(2n) {
  margin-left: 8%;
}

.match-star:nth-of-type(3),
.match-star:nth-of-type(4)  {
  top: 190px;
}

.match-star span {
  display: block;
  line-height: 15px;
  width: 95px;
  position: absolute;
  top: -20px;
  left: -10px;
}

.match-star div {
  width: 100%;
  height: 100%;
  background-image: url(../assets/match-star-selected.png);
  background-repeat: repeat-x;
  background-size: contain;
  background-position: 0 0;
}

.content-body-head {
  width: 100%;
  height: 29px;
  margin-top: 135px;
  padding: 0 24px;
  position: relative;
}

.content-body-head:before {
  content: '';
  display: block;
  position: absolute;
  box-sizing: border-box;
  top: 0;
  bottom: 0;
  left: 24px;
  right: 24px;
  margin: auto;
  border-radius: 3px;
  background: #9e95b3;
  opacity: 0.3;
  z-index: -1;
}

.content-body-head div {
  display: inline-block;
  line-height: 29px;
  width: 32%;
  position: relative;
  color: #9e95b3;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.content-body-head div.active {
  color: #ece5f3;
}

.content-body-head div:after {
  content: '';
  display: block;
  position: absolute;
  top: 29px;
  left: 50%;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 0px solid #9e95b3;
  opacity: 0.3;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.content-body-head div.active:after {
  border-top-width: 9px;
}

.content-body-head div:nth-of-type(1):before,
.content-body-head div:nth-of-type(3):before {
  content: '';
  display: block;
  position: absolute;
  top: 7px;
  left: -1px;
  width: 1px;
  height: 15px;
  background: #43385e;
}

.content-body-head div:nth-of-type(1):before {
  left: auto;
  right: -1px;
}

.match-text {
  position: relative;
  width: 300%;
  min-height: 1px;
}

.match-text-1 {
  -webkit-transform: translateX(-33.3333%);
          transform: translateX(-33.3333%);
}

.match-text-2 {
  -webkit-transform: translateX(-66.6666%);
          transform: translateX(-66.6666%);
}

.match-text>div {
  width: 33.3333%;
  min-height: 1px;
  float: left;
}

.match-text-transition {
  width: 100%;
  padding: 20px 28px 10px;
  text-align: justify;
  text-indent: 28px;
  -webkit-transition: opacity 0.2s linear;
  transition: opacity 0.2s linear;
  opacity: 1;
}

.match-text-enter, .match-text-leave {
  opacity: 0;
}



